<h2 id="企业微信" tabindex="-1"><a class="header-anchor" href="#企业微信" aria-hidden="true">#</a> 企业微信</h2>
<p>企业微信的使用与公众号以及其它几个应用的使用方式都是一致的，使用 <code>\EasyWeChat\Factory::work($config)</code> 来初始化：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'corp_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxxxxxxxxx'</span><span class="token punctuation">,</span>

    <span class="token string single-quoted-string">'agent_id'</span> <span class="token operator">=></span> <span class="token number">100020</span><span class="token punctuation">,</span> <span class="token comment">// 如果有 agend_id 则填写</span>
    <span class="token string single-quoted-string">'secret'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxx'</span><span class="token punctuation">,</span>

    <span class="token comment">// 指定 API 调用返回结果的类型：array(default)/collection/object/raw/自定义类名</span>
    <span class="token string single-quoted-string">'response_type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'array'</span><span class="token punctuation">,</span>

    <span class="token string single-quoted-string">'log'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'level'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'debug'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'file'</span> <span class="token operator">=></span> <span class="token constant">__DIR__</span><span class="token operator">.</span><span class="token string single-quoted-string">'/wechat.log'</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br></div></div><p>然后你就可以用 <code>$app</code> 来调用企业微信的服务了。</p>
